    },
    
    // ‰ΩøÁî®Êï∞ÊçÆÊúçÂä°ÁöÑÊñπÊ≥ï
    getSunDescription() {
      if (!this.calculationResults) return ''
      const sunSign = this.calculationResults.astrologyPositions.sun.sign
      return getSignDescription('sun', sunSign, this.currentLanguage)
    },
    
    getMoonDescription() {
      if (!this.calculationResults) return ''
